import { Flex, FlexProps } from '@chakra-ui/react'
import Icon from './Icon'

export default function CheckBox(props: {
  checked: boolean
  width?: string
  height?: string
  borderRadius?: string
  isDisabled?: boolean
  onClick: () => void
  cursor?: string
  wrapStyle?: FlexProps
  borderColor?: string
}) {
  const { checked, width, height, borderRadius, onClick, cursor = 'pointer', borderColor = 'border', wrapStyle = {}, isDisabled = false } = props
  return (
    <Flex
      align="center"
      justifyContent="center"
      background="checked_bg"
      border="1px solid"
      isDisabled={isDisabled}
      borderColor={borderColor}
      cursor={isDisabled ? 'not-allowed' : 'pointer'}
      width={width || '20px'}
      height={height || '20px'}
      borderRadius={borderRadius || '4px'}
      onClick={() => {
        if (isDisabled) return
        onClick()
      }}
      sx={{
        svg: {
          fill: 'primary !important'
        }
      }}
      {...wrapStyle}
    >
      {checked ? <Icon iconCursor={isDisabled ? 'not-allowed' : 'pointer'} xlinkHref="#icon-icon_check" /> : null}
    </Flex>
  )
}
